.theme-toggle-container {
  cursor: pointer;
  z-index: 100;
  left: 50%;
  top: 2rem;
  transform: translateX(-50%);
  position: fixed;
  border-radius: 50%;
  width: 44px;
  height: 44px;
  overflow: hidden;
  box-shadow: 0px 0px 25px var(--nav-mobile-outline);
}
.sunContainer {
  padding: 10px;
  background: linear-gradient(90deg, #e9e9e9 50%, #fdb927 51%, #fdb927 100%);
  border-radius: 50%;
  display: flex;
  color: rgb(66, 66, 66);
  position: absolute;
}
.activeSun {
  color: var(--on-background);
  z-index: 1;
  animation: sunSlideAnimation 1000ms forwards;
}

@keyframes sunSlideAnimation {
  0% {
    top: -45px;
  }
  100% {
    top: 0;
  }
}
.removeSun {
  color: rgb(66, 66, 66);
  animation: sunRemoveAnimation 1000ms forwards;
}

@keyframes sunRemoveAnimation {
  0% {
    top: 0px;
  }
  30% {
    top: 0px;
  }
  100% {
    top: -45px;
  }
}
.moonContainer {
  position: absolute;
  padding: 10px;
  background: linear-gradient(
    90deg,
    #121212 50%,
    rgba(150, 65, 255) 51%,
    rgba(150, 65, 255) 100%
  );
  border-radius: 50%;
  display: flex;
  color: rgba(255, 255, 255, 0.9);
}
.activeMoon {
  color: var(--on-background);
  z-index: 1;
  animation: moonSlideAnimation 1000ms forwards;
}
@keyframes moonSlideAnimation {
  0% {
    top: 45px;
  }
  100% {
    top: 0;
  }
}
.removeMoon {
  color: rgba(255, 255, 255, 0.9);
  animation: removeMoonAnimation 1000ms forwards;
}
@keyframes removeMoonAnimation {
  0% {
    top: 0px;
  }
  30% {
    top: 0px;
  }
  100% {
    top: 45px;
  }
}
